import React from 'react';
import './css/Registration.css'
import firebase from 'firebase/compat/app';
import "firebase/compat/auth";
import next_button from '../components/next_button.svg';
import {
    getAuth,
    createUserWithEmailAndPassword,
    updateProfile,
    sendEmailVerification,
    signInWithEmailAndPassword
} from "firebase/auth";

function Registration() {
    const firebaseConfig = {
        apiKey: "AIzaSyDsxe0dtYKVN0g_GJIHENK6NOVFYkgD6-Y",
        authDomain: "greenfarm-bf9d6.firebaseapp.com",
        projectId: "greenfarm-bf9d6",
        storageBucket: "greenfarm-bf9d6.appspot.com",
        messagingSenderId: "1022282893583",
        appId: "1:1022282893583:web:bdfbc702e654043d46167a"
    };
    firebase.initializeApp(firebaseConfig);
    const auth = getAuth();

    function register(email, password, name) {
        createUserWithEmailAndPassword(auth, email, password).then((userCredential) => {
            updateProfile(auth.currentUser, {displayName: name});
            sendEmailVerification(auth.currentUser).then(() => {
                let modal = document.getElementById('modal');
                let reg_form = document.getElementById('reg_form');
                modal.style.display = 'block';
                reg_form.style.filter = 'Blur(5px)'
            });
        }).catch((error) => {
            let error_field = document.getElementById('error_field');
            let error_message = document.getElementById('error_message');
            const errorMessage = error.message;
            error_message.innerText = errorMessage;
            error_field.style.display = 'block'
        })
    }

    return (
        <div className="reg_body">
            <div id="modal">
                <i className="fa fa-times" aria-hidden="true" onClick={() => {
                    let modal = document.getElementById('modal');
                    let form = document.getElementById('reg_form');
                    modal.style.display = 'none';
                    form.style.filter = 'none';
                    let email = document.getElementById('email_input').value;
                    let password = document.getElementById('password_input').value;
                    signInWithEmailAndPassword(auth, email, password).then((userCredential) => {
                        window.location.href = '/panel/dashboard';
                    })
                }}></i>
                <h4>На электронную почту отправлено письмо. Подтвердите аккаунт.</h4>
            </div>
            <div id="reg_form">
                <h1>Регистрация</h1>
                <div className="field">
                    <p>Название организации</p>
                    <input type="text" id="company_name"/>
                    <div className="group">
                        <div>
                            <p>Имя</p>
                            <input type="text" id="name"/>
                        </div>
                        <div>
                            <p>Фамилия</p>
                            <input type="text" id="second_name"/>
                        </div>
                    </div>
                    <div className="group">
                        <div>
                            <p>Email</p>
                            <input type="text" id="email_input"/>
                        </div>
                        <div>
                            <p>Пароль</p>
                            <input type="text" id="password_input"/>
                        </div>
                    </div>
                    <br/>
                </div>
                <button onClick={() => {
                    let email = document.getElementById('email_input').value;
                    let password = document.getElementById('password_input').value;
                    let name = document.getElementById('name').value;
                    let sub_name = document.getElementById('second_name').value;
                    register(email, password, (name + ' ' + sub_name))
                }} className="button_next"><img alt="" style={{pointerEvents: 'none'}} src={next_button}
                                                className="button_img"></img></button>
                <h4 id="error_field"><i className="fa fa-exclamation-circle" aria-hidden="true"></i><h4
                    id="error_message"></h4></h4>
                <span>Нажимая кнопку продолжения, Вы соглашаетесь с принятыми <a href="">условиями обработки данных</a></span>
            </div>
        </div>
    )
}

export default Registration;
