import React, {useState} from 'react';
import '../App.css';
import {Routes, Route, NavLink} from 'react-router-dom';

import Dashboard from './Dashboard';
import Storage from './Storage';
import Sales from './Sales';
import Staff from './Staff';
import Chat from './Chat';
import Settings from './Settings';

import logo_green from '../logo_green.svg';
import dashboard_icon from '../dashboard_icon.svg';
import storage_icon from '../storage_icon.svg';
import sales_icon from '../sales_icon.svg';
import staff_icon from '../staff_icon.svg';
import chat_icon from '../chat_icon.svg';
import settings_icon from '../settings_icon.svg';
import upgrade from '../upgrade.svg';
import download_app from '../download.svg';
import user_img from '../components/userq.jpg';
import userfield_button from '../components/userfield_button.svg';
import companyefficiency_sales_button from '../components/companyefficiency_sales-button.svg';
import companyefficiency_production_button from '../components/companyefficiency_production-button.svg';
import companyefficiency_clientflow_button from '../components/companyefficiency_clientflow-button.svg';
import usertasks_task1_image from '../components/usertasks_task1_image.svg';
import usertasks_task2_image from '../components/usertasks_task2_image.svg';

import {getAuth, onAuthStateChanged} from "firebase/auth";
import firebase from 'firebase/compat/app';
import "firebase/compat/auth";
import {useCookies} from "react-cookie";

function Application() {
    const [cookies, setCookie] = useCookies(['user']);
    const [user_e, setUser] = useState();
    const firebaseConfig = {
        apiKey: "AIzaSyDsxe0dtYKVN0g_GJIHENK6NOVFYkgD6-Y",
        authDomain: "greenfarm-bf9d6.firebaseapp.com",
        projectId: "greenfarm-bf9d6",
        storageBucket: "greenfarm-bf9d6.appspot.com",
        messagingSenderId: "1022282893583",
        appId: "1:1022282893583:web:bdfbc702e654043d46167a"
    };
    firebase.initializeApp(firebaseConfig);
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
        if (user) {
            setUser(user.displayName);
        }
    })
    window.onload = () => {
        if (cookies.isLogin === '0') {
            window.location.href = '/';
        }
    };
    return (
        <div className="main_body">
            <div className="left_panel">
                <img src={logo_green} alt="" height="48px"/>
                <br/>
                <button className="learning" onClick={() => {
                    setCookie('isLogin', '0', {path: '/'});
                    window.location.href = '/'
                }}>Обучение
                </button>
                <div className="menu">
                    <img src={dashboard_icon} alt="" height="20px"/><NavLink to="dashboard" className={'link_inactive'}
                                                                             activeclassname="selected">Панель<br/>управления</NavLink>
                    <img src={storage_icon} alt="" height="20px"/><NavLink to="storage" className={'link_inactive'}
                                                                           activeclassname="selected">Склад</NavLink>
                    <img src={sales_icon} alt="" height="20px"/><NavLink to="sales"
                                                                         className={'link_inactive'}>Продажи</NavLink>
                    <img src={staff_icon} alt="" height="20px"/><NavLink to="staff"
                                                                         className={'link_inactive'}>Персонал</NavLink>
                    <img src={chat_icon} alt="" height="20px"/><NavLink to="chat"
                                                                        className={'link_inactive'}>Чат</NavLink>
                    <img src={settings_icon} alt="" height="20px"/><NavLink to="settings"
                                                                            className={'link_inactive'}>Настройки</NavLink>
                </div>
                <div className="upgrade">
                    <img alt="" src={upgrade}/>
                    <button>Обновиться</button>
                </div>
            </div>
            <div className="main_screen">
                <Routes>
                    <Route path="/dashboard/*" element={<Dashboard/>}/>
                    <Route path="/storage" element={<Storage/>}/>
                    <Route path="/sales" element={<Sales/>}/>
                    <Route path="/staff" element={<Staff/>}/>
                    <Route path="/chat" element={<Chat/>}/>
                    <Route path="/settings" element={<Settings/>}/>
                </Routes>
            </div>
            <div className="right_panel">
                <div className="user_field">
                    <img style={{pointerEvents: 'none'}} className="user_img" alt="" src={user_img}/>
                    <div>
                        <p className="name_style">{user_e}</p>
                        <p className="subname_style">Директор компании</p>
                    </div>
                    <ul className="userfield_list">
                        <li>
                            <img style={{pointerEvents: 'none', marginLeft: '26px'}} alt="" src={userfield_button}/>
                            <ul>
                                <li><NavLink to="/dashboard">Themes</NavLink></li>
                                <li><NavLink to="/dashboard">Themes</NavLink></li>
                                <li><NavLink to="/dashboard">Выйти</NavLink></li>
                            </ul>
                        </li>
                    </ul>
                </div>
                <div className="companyefficiency">
                    <p>Производительность компании</p>
                    <div className="companyefficiency_sales">
                        <img style={{pointerEvents: 'none'}} className="companyefficiency_img" alt=""
                             src={companyefficiency_sales_button}/>
                        <div className="companyefficiency_property">
                            <h1>Продажи</h1>
                            <h2>Высокий</h2>
                        </div>
                        <div className="companyefficiency_prbar"></div>
                    </div>
                    <div className="companyefficiency_production">
                        <img style={{pointerEvents: 'none'}} className="companyefficiency_img" alt=""
                             src={companyefficiency_production_button}/>
                        <div className="companyefficiency_property">
                            <h1>Производство</h1>
                            <h2>Низкий</h2>
                        </div>
                        <div style={{backgroundColor: '#D5F5FB'}} className="companyefficiency_prbar"></div>
                    </div>
                    <div className="companyefficiency_clientflow">
                        <img style={{pointerEvents: 'none'}} className="companyefficiency_img" alt=""
                             src={companyefficiency_clientflow_button}/>
                        <div className="companyefficiency_property">
                            <h1>Клиентопоток</h1>
                            <h2>Средний</h2>
                        </div>
                        <div style={{backgroundColor: '#FFEFD6'}} className="companyefficiency_prbar"></div>
                    </div>
                </div>
                <div className="usertasks">
                    <p>Задания на день</p>
                    <div className="usertasks_task">
                        <img style={{pointerEvents: 'none'}} className="companyefficiency_img" alt=""
                             src={usertasks_task1_image}/>
                        <div className="companyefficiency_property">
                            <h1>Проверить почту</h1>
                            <h2>22 Ноя 2021, Понедельник</h2>
                        </div>
                    </div>
                    <div className="usertasks_task">
                        <img style={{pointerEvents: 'none'}} className="companyefficiency_img" alt=""
                             src={usertasks_task2_image}/>
                        <div className="companyefficiency_property">
                            <h1>Контроль автоматизации</h1>
                            <h2>22 Ноя 2021, Понедельник</h2>
                        </div>
                    </div>
                </div>
                <img style={{pointerEvents: 'none', marginTop: '40px'}} alt="" src={download_app}/>
            </div>
        </div>
    );
}

export default Application;
